h1{
    text-align: center;
}




.container{
    width: 460px;
    height: 540px;
    background-color: rgb(53, 53, 53);
    margin: auto;
    padding: 10px;
    border-radius: 10px;
    

}
P{
    margin-left: 10px;
    color: white;
    font-size: 1.5rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
#screen{
    background-color: rgb(170, 215, 215);
    height: 80px;
    width: 440px;
    margin: 5px;
    font-size: 4rem;
    text-align: right;
    border-radius: 10px;
    
}
.button{
    background-color: rgb(75, 74, 74);
    color: white;
    width: 92px;
    height: 70px;
    font-size: 2.5rem;
    margin: 10px;
    border-radius: 15px;
    
    
}
.clear{
    background-color: red;
}
.operator{
    background-color: rgb(136, 136, 136);
}
.button:hover{
    border: 5px solid greenyellow;
}